import React, { useState } from "react";

export default function Card({ card }: any ) {
  const [side, setSide] = useState<any | null>();

  const side1Length = card.fields.side1.length;
  const side2Length = card.fields.side2.length;
  const maxLength = Math.max(side1Length, side2Length);

  const getFontSize = () => {
    if (side1Length > 3) {
      return "small-font";
    } else if (side1Length > 2) {
      return "medium-font";
    } else {
      return "large-font";
    }
};

  function handleClick() {
    setSide(!side);
  }

  return (
    <div className={`card ${side ? "side" : ""}`} onClick={handleClick}>
      <small>
{/*        <span>卡號</span>
        {card.id}
*/}
      </small>
      {/* {side ? card.fields.side1 : card.fields.side2} */}
      <div className={`front ${getFontSize()}`}>{card.fields.side1}</div>
      <div className={`back ${getFontSize()}`}>
        <div>{card.fields.side2}</div>
        <div className="doublefont">{card.fields.side2}</div>
        <div className="bottomalign"><hr></hr>{card.fields.side2b}</div>
      </div>
    </div>
  );
}
