import React, { useState, useEffect } from "react";
import { useNavigate, useLocation } from 'react-router-dom';

import "./AppStyles.css";
import "./CardsStyles.css";

import Card from "./Card";

const ROOT = ""

export default function Cards() {

  const navigate = useNavigate();
  const location = useLocation();
  const flashcarddata = location.state.data;
  const randomState = location.state.randomState;

  const cards = flashcarddata.map((card: any) => {
    return <Card card={card} key={card.id} />;
  });

  const loading = <div className="loading">讀卡文件...</div>;

  
  // navigation in cards
  const [current, setCurrent] = useState(0);
  function previousCard() {
    setCurrent(current - 1);
  }
  function nextCard() {
    setCurrent(current + 1);
  }

  function topCard() {
    setCurrent(0);
  }

  function returnHome() {
    // navigate("/", {state: {randomState: randomState }});
    navigate("/", {state: {randomState: randomState}});
  }

  return (
    <div>

      {/* number of cards */}
      {flashcarddata && flashcarddata.length > 0 ? (
        <div className="cardNumber">
          卡 {current + 1} 共 {flashcarddata.length} 張
        </div>
      ) : (
        ""
      )}
      {/* /number of cards */}

      {/* render cards */}
      {flashcarddata && flashcarddata.length > 0 ? cards[current] : loading}
      {/* /render cards */}

      {/* render nav buttons */}
      <div className="nav2fr">
        {current > 0 ? (
          <button onClick={previousCard}>上張卡</button>
        ) : (
          <button className="disabled" disabled>
            上張卡
          </button>
        )}
        {current < flashcarddata.length - 1 ? (
          <button onClick={nextCard}>下張卡</button>
        ) : (
          <button className="disabled" disabled>
            下張卡
          </button>
        )}
        {/* /render nav buttons */}
      </div>
      <div className="nav2fr">
        <button className='short' onClick={topCard}>從頭開始</button>
        <button className='short' onClick={returnHome}>返回首頁</button>
      </div>
    </div>
  );
}
